.scheduleListDetail {
  display: flex;
  flex-direction: column;
  overflow: scroll;
  width: 230px;
  height: 92vh;
  border: 1px solid black;
}
.scheduleList {
  display: flex;
  align-items: center;
  flex-direction: column;
  font-size: 20px;
  margin: auto;
  margin-top: 10px;
  border: 1px solid black;
  border-radius: 10px;
  overflow: hidden;
  width: 190px;
  cursor: pointer;
}
.scheduleLayout {
  display: flex;
  flex-direction: column;
  position: relative;
}

.scheduleListRange {
  display: flex;
}

.schedulePhoto {
  width: 100%;
}
.scheduleTitle {
  align-self: center;
  margin: 5px;
}
.date {
  font-size: 10px;
}
.scheduleNav {
  list-style: none;
  display: flex;
  margin-left: 0px;
}
a {
  text-decoration: none;
  color: black;
  border: 1px solid black;
  margin: 5px;
  cursor: pointer;
}
.scheduleListAll {
  height: 180px;
}
.scheduleDetail {
  border: 1px solid rgb(240, 220, 185);
  background-color: rgb(240, 220, 185);
  display: flex;
  flex-direction: column;
  text-align: center;
  min-height: 100px;
  width: 200px;
  margin: 0px;
  margin-bottom: 20px;
}
.emptyList {
  margin: auto;
  color: rgb(255, 255, 255);
}

.scheduleAll {
  width: 230px;
  height: 92vh;
  overflow-y: scroll;
  border: 1px solid black;
}

.scheduleDateAll {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
}

.scheduleDateOnly {
  border-bottom: 3px solid white;
  color: white;
  background-color: rgb(30, 121, 191);
  text-align: center;
}

.switchBtn {
  position: fixed;
  top: 9px;
  left: 280px;
}
.switchBtn button {
  background-color: white;
  border: 1px solid black;
  margin: 5px;
  font-size: 16px;
  padding: 0;
  cursor: pointer;
}
.likeList {
  position: fixed;
  top: 54px;
  left: 255px;
  width: 200px;
  height: 92vh;
  overflow-y: scroll;
  border: 1px solid black;
}

// item

.item {
  // width: 195px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  margin: 6px;
  cursor: pointer;
  position: relative;
}
.itemList {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  overflow: hidden;
  margin: 6px;
  cursor: pointer;
  position: relative;
}
.itemPhoto {
  width: 100%;
  height: 100px;
  object-fit: cover;
}
.itemListDelete {
  position: absolute;
  top: 2px;
  right: 10px;
  cursor: pointer;
  display: none;
}
.itemLike {
  position: absolute;
  top: 10px;
  right: 10px;
  stroke: rgb(128, 128, 128);
  stroke-width: 2px;
  fill: rgb(255, 128, 191);
  fill-opacity: 1;
  cursor: pointer;
}
.searchLike {
  position: absolute;
  top: 10px;
  right: 10px;
  stroke: rgb(128, 128, 128);
  stroke-width: 2px;
  fill: rgb(255, 128, 191);
  fill-opacity: 1;
  cursor: pointer;
  display: none;
}
.searchAll {
  position: fixed;
  top: 45px;
  left: 250px;
  width: 200px;
  margin: 5px;
  background-color: white;
  z-index: 100;
}
.locationList {
  margin-top: 180px;
  margin-left: 15px;
  position: fixed;
  top: 0;
  left: 240px;
  width: 200px;
  height: 78vh;
  overflow-y: scroll;
  border: 1px solid black;
}

//map

.scheduleMap {
  position: absolute;
  top: 0;
  right: 0;
  width: 65%;
  background-color: green;
}
.markerText {
  margin-top: 5px;
}

// select
.itemTraffic {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}
.itemSelect {
  width: 70px;
}
